import React from 'react';
import Navbar from 'react-bootstrap/Navbar'
import { Form, Button, FormControl, Nav, NavDropdown } from "react-bootstrap";
import {
    Link
  } from "react-router-dom";

function Header() {
    return (
        <Navbar bg="dark" variant="dark" expand="lg">
            <div className="container">
                <Navbar.Brand as={Link} to="/">ATP</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link as={Link} to="/">Home</Nav.Link>
                        <Nav.Link as={Link} to="/assets">Assets</Nav.Link>
                        <NavDropdown className="mr-auto" title="Pages" id="basic-nav-dropdown">
                            <NavDropdown.Item as={Link} to="/contact">Contact</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/page2">Page 2</NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/page3">Page 3</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                    <Form inline>
                        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                        <Button variant="outline-success">Search</Button>
                    </Form>
                    <Button className="ml-2" variant="outline-primary" as={Link} to="/login">Login</Button>
                </Navbar.Collapse>
            </div>
        </Navbar>
    );
}

export default Header;