import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { useHistory } from 'react-router-dom';
import '../styles/LoginPage.css';
import { Row, Col } from 'react-bootstrap';
import authService from '../services/authService';

const sleep = ms => new Promise(resolve => setTimeout(resolve, ms));

function LoginForm({ setPage }) {
  const { register, handleSubmit, unregister, clearError, setError, errors } = useForm();
  const history = useHistory();
  const submitData = async data => {
    const success = await authService.login(data.email, data.password);
    if (success) {
      // redirect to asset page on successful login
      history.push('/assets');
    } else {
      alert('login error');
    }
  };
  return (
    <div className='login-page'>
      <div className='text-center'>
        <h1 className='display-2'>ATP</h1>
        <h4>Please login</h4>
      </div>
      <form className='login-form' onSubmit={handleSubmit(submitData)}>
        <div className='form-group'>
          <label htmlFor='email'>Email</label>
          <input
            className='form-control form-control-lg'
            name='email'
            placeholder='example@email.com'
            type='email'
            ref={register({ required: true })}
          />

          <label htmlFor='password'>Password</label>
          <input
            className='form-control form-control-lg'
            name='password'
            placeholder='password'
            type='password'
            ref={register({ required: true })}
          />
        </div>
        <div className='form-group text-center'>
          <input type='checkbox' name='remember' ref={register} />
          <label className='mx-2' htmlFor='remember'>
            Remember Me
          </label>
        </div>
        <div>
          <Row>
            <Col>
              <button className='btn btn-outline-primary btn-block' type='submit'>
                Login
              </button>
            </Col>
            <Col>
              <button
                className='btn btn-outline-success btn-block'
                type='submit'
                onClick={() => {
                  setPage(false);
                }}
              >
                Register
              </button>
            </Col>
          </Row>
        </div>
      </form>
    </div>
  );
}

function RegisterForm() {
  const { register, handleSubmit, unregister, clearError, setError, errors } = useForm();
  const history = useHistory();
  const submitData = async data => {
    let success = await authService.register(
      data.email,
      data.password,
      data.firstName,
      data.lastName,
      data.phone
    );
    if (success) {
      success = await authService.login(data.email, data.password);
      if (success) {
        history.push('/assets');
      } else {
        alert('login error');
      }
    } else {
      alert('register error');
    }
  };
  return (
    <div className='login-page'>
      <div className='text-center'>
        <h1 className='display-2'>ATP</h1>
        <h4>Registration</h4>
      </div>
      <div>
        <form className='register-form' onSubmit={handleSubmit(submitData)}>
          <div className='form-group'>
            <Row>
              <Col>
                <label htmlFor='firstName'>First Name</label>
                <input
                  className='form-control'
                  type='text'
                  placeholder='First Name'
                  name='firstName'
                  ref={register({ required: true })}
                />
              </Col>
              <Col>
                <label htmlFor='lastName'>Last Name</label>
                <input
                  className='form-control'
                  type='text'
                  placeholder='Last Name'
                  name='lastName'
                  ref={register({ required: true })}
                />
              </Col>
            </Row>
            <label htmlFor='email'>Email Address</label>
            <input
              className='form-control'
              type='email'
              placeholder='example@email.com'
              name='email'
              ref={register({ required: true })}
            />
            <label htmlFor='phone'>Phone Number</label>
            <input
              className='form-control'
              type='tel'
              placeholder='123-456-7890'
              name='phone'
              pattern='[0-9]{3}-[0-9]{3}-[0-9]{4}'
              ref={register}
            />
            <Row>
              <Col>
                <label htmlFor='password'>New Password</label>
                <input
                  className='form-control'
                  type='password'
                  placeholder='Password'
                  name='password'
                  ref={register({
                    required: true,
                    validate: {
                      correct: value =>
                        /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/.test(
                          value
                        ) === true
                    }
                  })}
                  onChange={async e => {
                    const value = e.target.value;
                    await sleep(1000);
                    if (
                      /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/.test(value)
                    ) {
                      clearError('Password');
                    } else {
                      setError(
                        'Password',
                        'notMatch',
                        'Password must be 8 characters long, contain one digit, upper, lower and special case character.'
                      );
                    }
                  }}
                />
              </Col>
            </Row>
            <div className='text-center'>
              {errors.Password && <p className='error-message'>{errors.Password.message}</p>}
            </div>
            <button className='btn btn-outline-success btn-block my-4' type='submit'>
              Register
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

function LoginPage() {
  const [page, setPage] = useState(true);

  if (page) {
    return <LoginForm setPage={setPage} />;
  } else {
    return <RegisterForm />;
  }
}

export default LoginPage;
